header{
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}

.header_container{
  text-align: center;
  height: 100%;
  position: relative;
}

.header-btns{
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

.header_socials{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header_socials::after{
  content: '';
  width: 1px;
  height: 2rem;
  background: var(--light-purple);
}

.me{
  background: linear-gradient(var(--navy-blue), var(--light-purple));
  width: 22rem;
  height: 22rem;
  position:absolute;
  border: 1px solid var(--light-purple);
  /*centering it*/
  left: calc(50% - 11rem);
  margin-top: 4rem;
  margin-bottom: 1rem;
  border-radius: 12rem 12rem 12rem 12rem;
  overflow: hidden;
  padding: 1rem;
  background-image: url(../../assets/portrait.png);
  background-position: absolute;
  background-position-x: 30%;
  background-position-y: 30%;
  background-color: var(--purple);
}

.scroll_down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

/*---Media Queries for medium devices like tablets---*/
@media screen and (max-width: 1024px) {
  header{
    height:68vh;
  }
}
/*---Media Queries for small devices like phones---*/
@media screen and (max-width: 600px) {
  header{
    height: 90vh;
  }
  .header_socials, .scroll_down{
    display: none;
  }
  .me{
    width: 20rem;
    height: 20rem;
    left: calc(50% - 10rem);
    background-position: absolute;
    background-position-x: 30%;
    background-position-y: 30%;
  }
}