/*Imported font from Google Fonts
Using Poppins font of weights 300,400,500,600
*/
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}
/*Root variables can be used anywhere in program*/
:root {
  --dark-navy: #0C134F;
  --navy-blue: #4452d8;
  --purple: #5C469C;
  --light-purple: #D4ADFC;
  --white: #fff;
  --light-gray: rgba(255,255,255,0.6);

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;

  --transition: all 400ms ease;
}

html{
  scroll-behavior: smooth;
}

::-webkit-scrollbar{
  display: none;
}

body{
  font-family: 'Poppins', sans-serif;
  background: var(--dark-navy);
  color: var(--white);
  line-height: 1.7;
  /*adds background fade
  background-image: url(./assets/background-fade.png);
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
  height:100vh;
  width:100%;
  */
}
/*---General Styles---*/
.container{
  width:var(--container-width-lg);
  margin: 0 auto;
}

h1, h2, h3, h4, h5 {
  font-weight: 500;
}

h1{
  font-size: 2.5rem;
}

section {
  margin-top: 8rem;
}

/*all h2 and h5 children of section tags*/
section > h2, section > h5 {
  text-align: center;
  color: var(--light-purple);
}

section > h2{
  color: var(--navy-blue);
  margin-bottom: 3rem;
}

.text-light {
  color: var(--light-purple);
}

a{
  color: var(--navy-blue);
  transition: var(--transition);
}

a:hover{
  color: var(--white);
}

.btn{
  width: max-content;
  display: inline-block;
  color: var(--navy-blue);
  /*0.75 top, bottom and 1.2 left,right*/
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--navy-blue);
  transition: var(--transition)
}

.btn:hover{
  background: var(--white);
  color: var(--dark-navy);
  border-color: transparent;
}

.btn-primary {
  background: var(--navy-blue);
  color: var(--dark-navy);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/*---Media Queries for medium devices like tablets---*/
@media screen and (max-width: 1024px) {
  .container{
    width: var(--container-width-md);
  }

  section{
    margin-top: 6rem;
    height: 100vh;
  }
}

/*---Media Queries for small devices like phones---*/
@media screen and (max-width: 600px) {
  .container{
    width: var(--container-width-sm);
  }

  section > h2{
    margin-bottom: 2rem;
  }
}