.mobile_portfolio {
  display: none;
}

.swiper {
  width: 70%;
}

.swiper_tile{
  padding:  0 0 2rem 0;
  align-items: center;
  justify-content: center;
}

.project_card {
  background: var(--navy-blue);
  box-shadow: 0 0 8px;
  width: 70%;
  text-align: center;
  aspect-ratio: 1/1;
  margin: 1rem auto;
  display: flex;
  flex-direction: column;
  border-radius: 2rem;
  aspect-ratio: 4/3;
}

.card_title {
  background: var(--light-gray);
  flex: 0.1;
  border-radius: 2rem 2rem 0 0 ;
  color: black;
}

.card_title h2 {
  margin: 1rem auto;
}

.project_image{
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.img_frame {
  display: flex;
  flex: 0.3;
  padding: 0.25rem 0 0 0;
  aspect-ratio: 1/1;
}

.img_frame img {
  flex-shrink: 0;
  min-width: 100%;
  min-height: 100%;
}

.card_description {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card_description p {
  display: flex;
  padding: 0 1rem;
}

.card_tech {
  background-color: var(--purple);
  color: white;
  flex: 0.25;
}

.card_buttons {
  display: flex;
  background: transparent;
  flex: 0.25;
  border-radius: 0 0 2rem 2rem;
  margin: 1rem 0 1rem 0;
}

.card_buttons a {
  margin: auto;
  background-color: var(--light-gray);
}

/*-----media queries for medium screens like tablets-----*/
@media screen and (max-width: 1024px) {
  .swiper {
    width: 90%;
  }
  .project_card {
    font-size: 0.85rem;
  }
}

/*-----media queries for small screens like phones-----*/
@media screen and (max-width: 600px) {
  .project_card {
    font-size: 0.5rem;
    width: 80%;
    flex: 1;
  }

  .card_tech{
    margin: 1rem 0 0 0;
  }
  .swiper{
    display: none;
  }

  .mobile_portfolio {
    display: table;
  }

  #projects {
    display: table;
  }
}
