#about {
  display: table;
}
.about_container{
  display:grid;
  grid-template-columns: 35% 50%;
  gap: 15%;
}

.about_me{
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(45deg, transparent, var(--navy-blue), transparent);
  display: grid;
  place-items: center;
}

.about_me_image{
  border-radius: 2rem;
  overflow: hidden;
  transform: rotate(10deg);
  width: 100%;
  aspect-ratio: 1/1;
  transition: var(--transition);
}

.about_me_image:hover {
  transform: rotate(0deg);
}
