.about_cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
}

.about_card {
  background: var(--light-purple);
  /*border: 3px solid var(--light-gray);*/
  border: 1px solid transparent;
  /*box-shadow: 8px 8px 6px black;*/
  padding: 2rem;
  border-radius: 1rem;
  text-align: center;
  /*flex: 1;*/
  color: black;
  transition: var(--transition);
}

.about_card:hover {
  background: transparent;
  border-color: var(--navy-blue);
  cursor: default;
  color: white;
}

.about_icon {
  font-size: 1.4rem;
  margin-bottom: 1rem;
}

.about_card h5{
  font-size: 0.9rem;
}

.about_card small {
  font-size: 0.7rem;
}

.about_content p {
  margin: 2rem 0 2.6rem;
  color: white;
}

/*-----media queries for medium screens like tablets-----*/
@media screen and (max-width: 1024px) {
  .about_container {
    grid-template-columns: 1fr;
    gap: 0;
  }

  .about_me {
    width: 50%;
    margin: 2rem auto 4rem;
  }

  .about_content{
    text-align: center;
  }

  .about_content p {
    margin: 1rem 0 1.5rem;
  }
}

/*-----media queries for small screens like phones-----*/
@media screen and (max-width: 600px) {
  .about_me {
    width: 65%;
    margin: 0 auto 3rem;
  }

  .about_cards {
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
  }

  .about_content {
    text-align: center;
  }

  .about_content p {
    margin: 1.5rem 0;
  }
}